.spinner-component{display:flex;align-items:center;flex-direction:column}.spinner-component .icon-spin{-webkit-animation:icon-spin 2s infinite linear;animation:icon-spin 2s infinite linear}@-webkit-keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}@keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}

.home-page .intro_section{background-image:url("/assets/jon-flobrant-oRUQsTX9Zv0-unsplash.jpg");width:100vw;height:100vh;margin:0;background-size:cover;background-attachment:fixed;display:flex;justify-content:center;flex-flow:column nowrap}.home-page .intro_section .counter_and_logo_outer_container{width:100%;padding-top:2em;position:relative;justify-content:center;display:flex;flex-flow:column-nowrap}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container{display:flex;flex-flow:column nowrap;margin:auto;margin-top:5vh;position:absolute;width:20%}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .logo{-webkit-filter:drop-shadow(2px 4px 10px white);filter:drop-shadow(2px 4px 10px white);width:100%;max-width:200px;margin:auto;-webkit-animation-name:fadeIn;animation-name:fadeIn;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:3s;animation-delay:3s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .treecounter{margin:0;text-align:center;width:100%;font-family:'Nunito', sans-serif;text-shadow:0 0 0.2em #000;color:white;-webkit-animation-name:fadeIn;animation-name:fadeIn;font-weight:bold;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:1s;animation-delay:1s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .treecounter p:first-child{font-size:1.5em}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .treecounter p{margin:0;font-size:1.25em}.home-page .intro_section .counter_and_logo_outer_container .progressbar-container{width:70%;height:35%;max-width:600px}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}

